body {
  text-align: center;
  height: 100vh;
  background-color: #f0f0f0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
  display: flex;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin: 10px;
  padding: 20px 40px;
  font-size: 20px;
  transition: background-color .3s;
}

button:disabled {
  cursor: not-allowed;
  background-color: #ccc;
}

button:hover {
  background-color: #0056b3;
}

